<template>
  <div class="container">
    <div class="box">
      <div class="author_img">
        <img src="../assets/logo.png" alt="" />
      </div>
      <el-form :model="from" class="form" label-width="0px" :rules="rules" ref="checkFrom">
        <el-form-item class="elform" prop="username">
          <el-input prefix-icon="iconfont icon-user" v-model="from.username" class="input"></el-input>
        </el-form-item>
        <el-form-item class="elform" prop="password">
          <el-input class="input" prefix-icon="iconfont icon-3702mima" v-model="from.password" type="password"></el-input>
        </el-form-item>
        <el-form-item class="elbutton elform">
          <el-button type="primary" @click="login">登录</el-button>
          <el-button type="info" @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      from: {
        username: 'admin',
        password: '123456'
      },
      rules: {
        username: [{ required: true, message: '请输入账号', trigger: 'blur' }, { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }, { min: 6, max: 12, message: '长度在 6 到 12个字符', trigger: 'blur' }]
      }
    }
  },
  methods: {
    created() {
      this.$message({
        duration: 1000
      })
    },
    login() {
      this.$refs.checkFrom.validate(async valid => {
        if (!valid) return
        let { data: res } = await this.$http.post('login', this.from)
        if (res.meta.status !== 200) {
          return this.$message.error('登录失败')
        }
        //  将后台返回的token添加到本地
        sessionStorage.setItem('token', res.data.token)
        this.$message.success('登录成功')
        this.$router.push('/home')
      })
    },
    reset() {
      this.$refs.checkFrom.resetFields()
    }
  }
}
</script>
<style lang="less" scoped>
.container {
  height: 100%;
  background-color: #2b4b6b;
}
.box {
  width: 450px;
  height: 300px;
  background-color: #ffffff;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid #ccc;
  border-radius: 5px;
}
.author_img {
  position: absolute;
  width: 130px;
  height: 130px;
  border-radius: 50%;
  padding: 10px;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;

  img {
    width: 130px;
    border-radius: 50%;
    background-color: #eee;
  }
}
.form {
  width: 100%;
  position: absolute;
  box-sizing: border-box;
  bottom: 0;
}
.elform {
  padding: 0 20px;
}
.elbutton {
  float: right;
}
</style>
