<template>
  <el-container class="container">
    <el-header class="header">
      <div>
        <img src="../assets/heima.png" alt="" />
        <span>电商后台管理系统</span>
      </div>
      <el-button type="info" @click="out" class="button">退出</el-button>
    </el-header>
    <el-container>
      <el-aside :width="iscollapse ? '64px' : '200px '" class="aside">
        <div class="collapse" @click="collapse">|||</div>
        <el-menu
          background-color="#333744"
          text-color="#fff"
          active-text-color="#409EFF"
          unique-opened
          class="menu"
          :collapse="iscollapse"
          :collapse-transition="false"
          router
          :default-active="activePath"
        >
          <el-submenu :index="item.id + ''" v-for="item in Meun" :key="item.id">
            <!-- 一级导航 -->
            <template slot="title">
              <i :class="icon[item.id]"></i>
              <span>{{ item.authName }}</span>
            </template>

            <!-- 二级导航 -->
            <el-menu-item :index="'/' + items.path" v-for="items in item.children" :key="items.id" @click="changeActive('/' + items.path)">
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span>{{ items.authName }}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-container>
        <el-main class="main">
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </el-container>
</template>
<script>
import { constants } from 'crypto'
export default {
  data() {
    return {
      Meun: [],
      icon: {
        '125': 'iconfont icon-users',
        '103': 'iconfont icon-tijikongjian',
        '101': 'iconfont icon-shangpin',
        '102': 'iconfont icon-danju',
        '145': 'iconfont icon-baobiao'
      },
      iscollapse: false,
      activePath: ''
    }
  },
  created() {
    this.getMeun()
    this.activePath = window.sessionStorage.getItem('activePath')
  },
  methods: {
    out() {
      sessionStorage.clear()
      this.$router.push('/login')
    },
    async getMeun() {
      const { data: res } = await this.$http.get('menus')
      this.Meun = res.data
      //console.log(res.data)
    },
    collapse() {
      this.iscollapse = !this.iscollapse
    },
    changeActive(current) {
      window.sessionStorage.setItem('activePath', current)
      this.activePath = current
    }
  }
}
</script>
<style lang="less" scoped>
.container {
  height: 100%;
  width: 100%;
}
.header {
  background-color: #373d41;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #fff;
  font-size: 20px;
  > div {
    display: flex;
    align-items: center;
  }
  span {
    margin-left: 20px;
  }
}
.aside {
  background-color: #333744;
}
.footer {
  background-color: #333744;
}
.iconfont {
  margin-right: 5px;
}
.menu {
  border-right: none;
}
.collapse {
  text-align: center;
  color: #fff;
  line-height: 24px;
  font-size: 12px;
  letter-spacing: 0.2em;
  cursor: pointer;
  background-color: #4a5064;
}
.main {
  background-color: #eaedf1;
}
</style>
